import { useState } from "react";
import OneBankDaybookCardEdit from "./OneBankDaybookCardEdit";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import {
  useFinanceDaybookRemoveExcel,
  useFinanceDaybookUpdateExcel,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";

const OneBankDaybookCardMenu = ({ onClose, xlsxId, bankId, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [financeDaybookRemoveExcel] = useFinanceDaybookRemoveExcel();
  const [financeDaybookUpdateExcel] = useFinanceDaybookUpdateExcel();

  const onDelete = () => {
    if (bankId && xlsxId?._id) {
      setDisabled((pre) => !pre);
      financeDaybookRemoveExcel({
        baid: bankId,
        bodyParameter: {
          dbid: xlsxId?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (bankId && xlsxId?._id) {
      onOpenEdit();
      setDisabled((pre) => !pre);
      financeDaybookUpdateExcel({
        baid: bankId,
        bodyParameter: {
          dbid: xlsxId?._id,
          excel_file_name: val,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return edit ? (
    <OneBankDaybookCardEdit
      onClose={onOpenEdit}
      onEditAction={onEdit}
      xlsxId={xlsxId}
    />
  ) : (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onOpenEdit}>{t("edit")}</h6>
      <h6 onClick={onDelete}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default OneBankDaybookCardMenu;
