import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useCallback, useEffect, useState } from "react";
import OneBankDaybookFilter from "./OneBankDaybookFilter";
import { useFinanceBankDaybookGeneratList } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import OneBankDaybookCard from "./OneBankDaybookCard";
const OneBankDaybook = ({ fid, bankId }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    financeBankDaybookGeneratList,
    financeBankDaybookGeneratListLoading,
    financeBankDaybookGeneratListRefetch,
  } = useFinanceBankDaybookGeneratList({
    data: {
      baid: bankId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !bankId,
  });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeBankDaybookGeneratList?.list?.length) {
      let obj = nextPage(financeBankDaybookGeneratList?.list?.length);
      setState(() => obj);
    }
  }, [financeBankDaybookGeneratList?.list]);
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    financeBankDaybookGeneratListRefetch();
  };
  return (
    <>
      <UserMemberContentWrapper
        customStyle={{
          padding: "0",
        }}
      >
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
          <CreateButton
            label="filter"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
            }}
            onAction={onToggle}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      {financeBankDaybookGeneratList?.list?.map((excel, index) =>
        financeBankDaybookGeneratList?.list?.length === index + 1 ? (
          <div key={excel?._id} ref={ref}>
            <OneBankDaybookCard
              excel={excel}
              bankId={bankId}
              onRefetch={onRefetch}
            />
          </div>
        ) : (
          <OneBankDaybookCard
            excel={excel}
            key={excel?._id}
            bankId={bankId}
            onRefetch={onRefetch}
          />
        )
      )}
      {financeBankDaybookGeneratListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        financeBankDaybookGeneratList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No daybook found related this search."
          />
        )
      ) : (
        financeBankDaybookGeneratList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No daybook added."
          />
        )
      )}

      {add && (
        <OneBankDaybookFilter onClose={onToggle} bankId={bankId} fid={fid} />
      )}
    </>
  );
};

export default OneBankDaybook;
